import Vue from 'vue';
import VueRouter from 'vue-router';
import Login from '../views/Authentication/Login.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
  },
  {
    path: '/register',
    name: 'about',
    component: () => import('../views/Authentication/Register.vue'),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
